import { useCounter } from "hooks/use-counter";
import { useCallback } from "react";
import { Item } from "./Item";
import { DataType } from "types";
import * as Icon from "./icons";
import getStarted from "./get-started-screen.svg";
import online from "./online-screen.svg";
import congrat from "./congrat-screen.svg";
import styles from "../../styles.module.scss";

export type StepsType = {
    id: number;
    imageSrc: string;
} & DataType;

export const stepsData: Array<StepsType> = [
    {
        id: 0,
        imageSrc: getStarted,
        icon: <Icon.Devices />,
        title: "Create a profile",
        text: "Download the app for free, Sign up for an account and let us to get know you",
    },
    {
        id: 1,
        imageSrc: online,
        icon: <Icon.FactCheck />,
        title: "Apply for a loan",
        text: "Select the loan amount and term on the calculator that suits for you",
    },
    {
        id: 2,
        imageSrc: congrat,
        icon: <Icon.Card />,
        title: "Get money!",
        text: "If you qualify your cash will be disbursed within a minute. Just add your account number",
    },
];

export const Steps = () => {
    const { state, dispatch } = useCounter(stepsData);

    const toggleActive = useCallback(
        (index: number) => {
            dispatch({ type: "toggle", count: stepsData[index].id });
        },
        [dispatch]
    );

    return (
        <div className={styles.steps}>
            {stepsData.map((item, index) => (
                <Item
                    key={item.id}
                    data={item}
                    activeStage={state.count}
                    onChange={toggleActive}
                    index={index}
                />
            ))}
        </div>
    );
};
