import React from "react";
import { StepsType } from "./Steps";
import classNames from "classnames";
import styles from "../../styles.module.scss";

export const Item: React.FC<{
    data: StepsType;
    activeStage: number;
    onChange: (index: number) => void;
    index: number;
}> = ({ data, activeStage, onChange, index }) => {
    return (
        <div
            className={classNames(
                styles.step,
                activeStage === index && `${styles.active}`
            )}
            onClick={() => onChange(index)}
        >
            <div className={styles.overlay}>{data.icon}</div>
            <div>
                <h3>{data.title}</h3>
                <p className="mobile:text-[14px]">{data.text}</p>
            </div>
            <img
                src={data.imageSrc}
                alt={data.title}
                width="323"
                height="622"
            />
        </div>
    );
};
