import { useCallback, useEffect, useReducer } from "react";

type ActionType = "increase" | "decrease" | "start" | "toggle";

type State = {
    type: ActionType;
    count: number;
};

const initialState: State = { type: "start", count: 0 };

type IncreaseAction = {
    type: "increase";
};

type DecreaseAction = {
    type: "decrease";
};

type StartAction = {
    type: "start";
};

type ToggleAction = {
    type: "toggle";
    count: number;
};

type Action = IncreaseAction | DecreaseAction | StartAction | ToggleAction;

const reducer = (state: State, action: Action) => {
    switch (action.type) {
        case "increase":
            return { type: action.type, count: state.count + 1 };
        case "decrease":
            return { type: action.type, count: state.count - 1 };
        case "start":
            return { type: action.type, count: initialState.count };
        case "toggle":
            return { type: action.type, count: action.count };
    }
};

export function useCounter(data: Array<any>) {
    const [state, dispatch] = useReducer(reducer, initialState);

    const decrementCounter = useCallback(() => {
        if (state.count !== 0) {
            dispatch({ type: "decrease" });
        }
    }, [state.count, dispatch]);

    const incrementCounter = useCallback(() => {
        if (state.count !== data.length - 1) {
            dispatch({ type: "increase" });
        }
    }, [data.length, dispatch, state]);

    useEffect(() => {
        const interval = setInterval(() => {
            dispatch(
                state.count === data.length - 1
                    ? { type: "start" }
                    : { type: "increase" }
            );
            return data[state.count];
        }, 4000);

        return () => clearInterval(interval);
    }, [state.count, dispatch, data]);

    return { state, dispatch, decrementCounter, incrementCounter };
}
